import React, { useState } from "react";
import { Button, Table } from "semantic-ui-react";
import { useAuthors, useArticles } from "../../hooks";
import { author as authorSceleton } from "../../sceletons";
export default function Authors() {
  const [authors, setAuthors] = useAuthors();
  const [author, setAuthor] = useState();
  const [articles] = useArticles();

  function deleteAuthor(id) {
    const _authors = [...authors];
    _authors[id] = undefined;
    setAuthors(_authors);
  }

  return (
    <div>
      <Button
        onClick={() => setAuthors({ authorSceleton, id: authors.length })}
        primary
      >
        Neuer Autor
      </Button>
      <Table celled>
        <Table.Header>
          <Table.Row>
            <Table.HeaderCell>Id</Table.HeaderCell>
            <Table.HeaderCell>Name</Table.HeaderCell>
            <Table.HeaderCell>Artikel</Table.HeaderCell>
            <Table.HeaderCell>Kontakt</Table.HeaderCell>
            <Table.HeaderCell></Table.HeaderCell>
          </Table.Row>
        </Table.Header>

        <Table.Body>
          {authors.map((a, i) => (
            <Table.Row>
              <Table.Cell>{a.id}</Table.Cell>
              <Table.Cell>{a.name}</Table.Cell>
              <Table.Cell>
                {a.books.map((id) => (
                  <p>{articles[id].name}</p>
                ))}
              </Table.Cell>
              <Table.Cell>{a.contact}</Table.Cell>
              <Table.Cell>
                <Button onClick={() => setAuthor(a)} icon="eye"></Button>
                <Button onClick={() => deleteAuthor(i)} icon="delete"></Button>
              </Table.Cell>
            </Table.Row>
          ))}
        </Table.Body>
      </Table>
    </div>
  );
}
